.About {
  margin: 0 70px;
  margin-bottom: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  padding: 10vh;
  background: linear-gradient(#9198e550,#75BA6750);
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  box-shadow: 0 0 20px #0f0f0fa2;
  margin-top: 140px;

  filter: blur(0px);
  .container {
    max-width: 800px;
    padding: 0 20px;
    text-align: center;
    h1 {
      font-size: 3rem;
      margin-bottom: 20px;
      text-shadow:0 0 1px #000000
    }
    p {
      font-size: 1.5rem;
      line-height: 2rem;
      b {
        color: #000000;
        text-shadow:0 0 1px #000000
      }
    }
  } 
  &__title {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  &__text {
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 20px;
  }
}
